<template>
  <v-sheet>
    <v-card>
      <v-card-title></v-card-title>
      <v-card-text>
        <v-text-field label="" placeholder="" v-model="nickname" />
      </v-card-text>
      <v-card-actions>
        <v-btn text color="primary" @click="signup({jwt: jwt, nickname: nickname})"></v-btn>
      </v-card-actions>
    </v-card>
  </v-sheet>
</template>

<script>
import { mapActions } from "vuex";

export default {
  name: "CallbackSignup",
  data() {
    return {
      jwt: "",
      nickname: ""
    };
  },
  mounted() {
    if(!this.$route.query.token) return this.$router.push({ path: '/login', replace: true });
    this.jwt = this.$route.query.token;
    this.$router.replace("/callback/signup");
  },
  methods: {
    ...mapActions({
      signup: "auth/signup",
    }),
  },
  computed: {},
};
</script>